import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col } from "react-bootstrap";

const Orari = (props) => {
    return (
        <Container>
            <Row>
                <Col>Lunedì</Col>
                <Col>13:00 - 15:00</Col>
            </Row>
            <Row>
                <Col>Martedì</Col>
                <Col>13:00 - 15:00</Col>
            </Row>
            <Row>
                <Col>Mercoledì</Col>
                <Col>13:00 - 15:00</Col>
            </Row>
            <Row>
                <Col>Giovedì</Col>
                <Col>13:00 - 15:00</Col>
            </Row>
            <Row>
                <Col>Venerdì</Col>
                <Col>13:00 - 15:00</Col>
            </Row>
        </Container>
    );
};

export default Orari;