import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';

import HeaderNav from './components/HeaderNav.js';
import Avviso from './components/Avviso.js';
import Orari from './components/Orari.js';
import Contatti from './components/Contatti.js';

import './App.css';

let AVVISI = [
    {id:3, title: 'Vaccini2', description: 'Ciao a tutti2', datetime:'2021-04-21'},
    {id:2, title: 'Vaccini', description: 'Ciao a tutti', datetime:'2021-04-21'},
    {id:1, title: 'Example', description: 'This is a test', datetime:'2021-04-20'},
    {id:0, title: 'Test', description: 'This is a test', datetime:'2020-07-01'}
];

const App = () => {
    return (
        <body>
            <Router>
                <HeaderNav />
                <Switch>
                    <Route exact path="/" render={() => {
                        return (<Redirect to="/home"/>);
                    }}/>
                    <Route exact path="/home">
                        {AVVISI.map(a => <Avviso key={a.id} avviso={a}/>)}
                    </Route>
                    <Route exact path="/orari">
                        <Orari />
                    </Route>
                    <Route exact path="/contatti">
                        <Contatti />
                    </Route>
                </Switch>
            </Router>
        </body>
    );
};

export default App;
