import "bootstrap/dist/css/bootstrap.min.css";
import { Navbar, Nav } from "react-bootstrap";

import "./HeaderNav.css";

const HeaderNav = (props) => {
    return (
        <Navbar bg="dark" variant="dark" expand="sm" id="header-nav">
            <Navbar.Brand href="#home" id="logo">
                <h3>Studi Medici Riuniti</h3>
                <h6>Dott.ssa Ceo Grazia</h6>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto" id="links">
                    <Nav.Link href="/home" className="navlink" >Home</Nav.Link>
                    <Nav.Link href="/orari" className="navlink" >Orari</Nav.Link>
                    <Nav.Link href="/contatti" className="navlink" >Contatti</Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    );
};

export default HeaderNav;
