import "bootstrap/dist/css/bootstrap.min.css";
import { Card, Badge } from "react-bootstrap";

import dayjs from 'dayjs';

import './Avviso.css';

const Avviso = (props) => {

    const computeTime = (datetime) => {
        const date1 = dayjs();
        let diff = parseInt(date1.diff(datetime, 'day'));

        if (diff < 0) return undefined;

        if (diff === 0) return 'Oggi';
        else if (diff === 1) return 'Ieri';
        else if (diff < 31) return String(diff) + ' giorni fa';
        else {
            diff = parseInt(date1.diff(datetime, 'month'));
            if (diff === 1) return '1 mese fa';
            else if (diff < 12) return String(diff) + ' mesi fa';
            else {
                diff = parseInt(date1.diff(datetime, 'year'));
                if (diff === 1) return '1 anno fa';
                else return String(diff) + ' anni fa';
            }
        } 
    }; 

    const showBadge = (datetime) => {
        if (computeTime(datetime) === 'Oggi')
            return (<Badge variant="secondary">New</Badge>);
        return (<></>);
    }

    return (
        <Card className="text-center">
            <Card.Body>
                <Card.Title>{props.avviso.title}  {showBadge(props.avviso.datetime)}</Card.Title>
                <Card.Text>
                    {props.avviso.description}
                </Card.Text>
            </Card.Body>
            <Card.Footer className="text-muted">{computeTime(props.avviso.datetime)}</Card.Footer>
        </Card>
    );
};

export default Avviso;